import React, { Component, Fragment } from 'react';
import { Card, Row, Table, Col, message } from 'antd';
import Page from 'components/LayoutComponents/Page';
// import { BtnAddNew, BtnDelete } from 'components/common/button';
import { getStudentInTO } from 'services/testOrganizing';
import { getStudentInTestPackage, createTestPackage, deleteTestPackage } from 'services/testPackage';
// import { BtnEdit, BtnDelete } from 'components/common/button';
// import { Link } from 'react-router-dom';
// import routeConstant from 'routes/route/routeConstant';
import SearchBar from './SearchBar';
import SearchBar1 from './SearchBar1';

// import { policyObjects } from 'dump/data';

const column1 = [
  {
    title: 'Số báo danh',
    dataIndex: 'testCode',
    key: 'testCode',
  },
  {
    title: 'Mã sinh viên',
    dataIndex: 'studentCode',
    key: 'studentCode',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Lớp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'Phòng thi',
    dataIndex: 'room',
    key: 'room',
  },
  {
    title: 'Ghi chú',
    dataIndex: 'note',
    key: 'note',
  },
];
const column2 = [
  {
    title: 'Số báo danh',
    dataIndex: 'testCode',
    key: 'testCode',
  },
  {
    title: 'Số phách',
    dataIndex: 'sp',
    key: 'sp',
  },
  {
    title: 'Mã sinh viên',
    dataIndex: 'studentCode',
    key: 'studentCode',
  },
  {
    title: 'Họ và tên',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Lớp',
    dataIndex: 'class',
    key: 'class',
  },
  {
    title: 'Phòng thi',
    dataIndex: 'room',
    key: 'room',
  },
  {
    title: 'Ghi chú',
    dataIndex: 'note',
    key: 'note',
  },
];

class ListCadencePage extends Component {
  state = {
    tableData: [],
    tableData2: {},
    selectedAddRows: [],
    selectedRemoveRows: [],
    selectedRowKeys: [],
    tOId: false,
    packageNo: false
  };

  addItems = () => {
    const  tableData2 = {...this.state.tableData2};
    console.log(tableData2);
    this.state.selectedAddRows.forEach(item => {
      tableData2[item.id] = item;
    });
    this.setState({ tableData2 });
  }

  handleCreateTestPackage = async ({ packageNo, headSignal, fromNo }) => {
    try {
      const students = {...this.state.tableData2};
      let count = Number(fromNo);
      const resquestData = {
        toId: this.state.tOId,
        packageNo,
        headSignal,
        fromNo
      };
      resquestData.students = Object.entries(students).map(studentEntry => {
        count += 1;
        return ({
          ...studentEntry[1],
          detachHeadNo: `${headSignal}${count}`
        })
      })

      const res = await createTestPackage(resquestData);
      if(res.data && res.data.items) {
        this.setState({ tableData: res.data.items });
      }
    } catch (err) {
      console.log(err);
    } finally {
      console.log("finally");
    }
  }

  handleDeleteTestPackage = async () => {
    try {
      const { tOId, packageNo } = this.state;
      const res = await deleteTestPackage({ toId: tOId, packageNo });
      if(res.data && res.data.items) {
        this.setState({ tableData: res.data.items });
      }
      message.success('Xoá thành công');
    } catch (err) {
      message.error('Có lỗi xãy ra');
    }
  }

  handleSearch = async ({ tOId }) => {
    try {
      this.setState({ tOId });
      const res = await getStudentInTO({ tOId });
      if(res.data && res.data.items) {
        this.setState({ tableData: res.data.items });
      }
    } catch (err) {
      console.log(err);
    } finally {
      console.log("finally");
    }
  }

  handleSearchStudentInTP = async packageNo => {
    try {
      this.setState({ packageNo });
      const res = await getStudentInTestPackage({ packageNo, tOId: this.state.tOId });
      console.log(res);
      if(res.data && res.data.items) {
        // this.setState({ tableData: res.data.items });
      }
    } catch (err) {
      console.log(err);
    } finally {
      console.log("finally");
    }
  }

  removeItems = () => {
    const  tableData2 = {...this.state.tableData2};

    this.state.selectedRemoveRows.forEach(item => {
      delete tableData2[item.id];
    });

    this.setState({ tableData2, selectedRowKeys: [] });
  }

  render() {
    const { tableData, tableData2 } = this.state;
    this.rowSelection1 = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        this.setState({ selectedAddRows: selectedRows });
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };

    this.rowSelection2 = {
      selectedRowKeys: this.state.selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
        this.setState({ selectedRemoveRows: selectedRows, selectedRowKeys });
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
    return (
      <Fragment>
        <Card title="Đánh phách">
          <Row>
            <Col span={24}>
              <SearchBar handleSearch={this.handleSearch} />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table rowSelection={this.rowSelection1} bordered dataSource={tableData} columns={column1} />
            </Col>
          </Row>
        </Card>
        <Card title="Danh sách đánh phách">
          <SearchBar1
            handleSearch={this.handleSearchStudentInTP}
            handleAdd={this.addItems}
            handleDelete={this.removeItems}
            handleCreateTestPackage={this.handleCreateTestPackage}
            handleDeleteTestPackage={this.handleDeleteTestPackage}
          />
          <Row>
            <Col span={24}>
              <Table
                rowSelection={this.rowSelection2}
                bordered
                dataSource={Object.entries(tableData2).map(tableEntry => tableEntry[1])}
                columns={column2}
              />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListCadencePage />
  </Page>
);



